<template lang="html">
    <div
        v-if="orders.length"
        class="row align-center"
    >
        <div class="col-6 specific_order">
            <form-switch
                id="chk_linked"
                v-model="chk_linked"
            />
            <span class="form-switch_text">{{ label }}</span>
        </div>
        <!-- orderid -->
        <div
            v-if="chk_linked"
            class="col-6"
            :class="errors ? 'form-select_order' : ''"
        >
            <form-select
                v-model="$v.linked_to_order.$model"
                label=" Pretype order ID or choose from the list..."
                :items="orders"
                :err="Boolean(errors)"
                name="linked_to_order"
            />
            <div
                v-if="errors"
                class="form-select__error"
            >
                {{ errors }}
            </div>
        </div>
    </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/support/Rules'
import { formMessages } from '@/validation/support/Messages'
import Api from '@/helpers/api/index.js';
import filtersMixin from '@/mixins/filtersMixin.js'

import FormSwitch from '@/components/common/form-elements/FormSwitch';
import FormSelect from '@/components/common/form-elements/FormSelectOrder';
import {
    mapSupportFields
} from '@/store/modules/support';

export default {
    components: {
        FormSwitch,
        FormSelect
    },
    mixins: [
        filtersMixin,
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    props: {
        label: {
            type: String
        },
        errors: {
            type: String
        }
    },
    data: () => ({
        orders: [],
        inputLoading: false,
        search: null
    }),
    computed: {
        ...mapSupportFields([
            'form_data.chk_linked',
            'form_data.linked_to_order'
        ])
    },
    watch: {
        search(val) {
            val && val !== this.select && val.length > 3 && this.querySelections(val)
        }
    },
    async mounted() {
        this.getInitialOrdersList()
    },
    methods: {
        validationMessage: validationMessage(formMessages),
        async querySelections(queryString) {
            const vm = this
            this.inputLoading = true

            await Api.post('/api/cp/order/list-orders-reference-component', {
                query: queryString
            })
                .then((res) => {
                    vm.orders = res.data
                })
                .catch((err) => {
                    console.log(err)
                })
                .finally(() => {
                    this.inputLoading = false
                })
        },
        async getInitialOrdersList() {
            const vm = this
            this.inputLoading = true

            await Api.post('/api/cp/order/list-orders', {
                per_page: 100
            })
                .then((res) => {
                    vm.orders = res.data
                })
                .catch((err) => {
                    console.log(err)
                })
                .finally(() => {
                    this.inputLoading = false
                    vm.$forceUpdate()
                })
        }

    }
}
</script>

<style lang="scss">
    .quote-form__form-col-100 {
        width: 100%;
    }
    .form-select_order {
        position: relative;
        .form-select {
            border: 1px solid red;
        }
    }
</style>
