<template>
    <div
        class="support-form"
        :class="auth ? 'support-form--auth' : ''"
    >
        <div
            v-if="!propOrderId"
            class="support-form_header"
        >
            <p>
                {{ auth ? `Hi ${firstname}, how can we help?` : 'Support Request' }}
            </p>
            <span
                class="btn-base btn-base_colored btn-base_normal"
                @click="openModal('confirm-modal')"
            >Clear</span>
        </div>

        <div class="support-form_body">
            <div
                v-if="!auth && !propOrderId"
                class="row support-form_body--alert"
            >
                <alert-circle-outline />
                <p>
                    It is suggested that you
                    <router-link :to="{name: 'login'}">
                        log in
                    </router-link>
                    to your account for better user experience. You will be able to reference an existing order.
                </p>
            </div>

            <div
                v-if="!auth"
                class="row"
            >
                <div class="col-6">
                    <form-input
                        v-model="$v.fullname.$model"
                        legend="Fullname"
                        type="text"
                        name="fullname"
                        prepend-icon="AccountOutline"
                        placeholder="Full Name"
                        :field.sync="fullname"
                        :errors="validationMessage($v.fullname)"
                        @input.native="$v.fullname.$touch()"
                        @blur.native="$v.fullname.$touch()"
                    />
                </div>
                <div class="col-6">
                    <form-input
                        v-model.trim="$v.email.$model"
                        legend="E-mail"
                        type="text"
                        name="email"
                        prepend-icon="EmailOutlineIcon"
                        placeholder="Email"
                        :field.sync="email"
                        :errors="validationMessage($v.email)"
                        @input.native="$v.email.$touch()"
                        @blur.native="$v.email.$touch()"
                    />
                </div>
            </div>

            <div
                v-if="!auth && !verified"
                class="row"
            >
                <div class="support-form_field">
                    <form-switch
                        id="chk_phone_bypass"
                        v-model="chk_phone_bypass"
                    />
                    <span class="form-switch_text">I am unable to verify my phone</span>
                </div>
            </div>

            <div
                v-if="!auth && !chk_phone_bypass"
                class="row"
            >
                <phone-number
                    @phoneValidated="validatePhone"
                />
            </div>

            <div
                v-if="!chk_phone_bypass"
                class="row"
            >
                <div class="support-form_field">
                    <form-switch
                        id="chk_callback"
                        v-model="chk_callback"
                    />
                    <span class="form-switch_text">I would like a phone call from a support officer</span>
                </div>
            </div>

            <reference-order
                v-if="auth"
                label="I have a problem with a specific order"
                :errors="validationMessage($v.linked_to_order)"
                @change.native="$v.linked_to_order.$touch()"
                @blur.native="$v.linked_to_order.$touch()"
            />

            <div class="row">
                <div class="col-12">
                    <form-input
                        v-model="$v.subject.$model"
                        legend="Subject"
                        type="text"
                        name="subject"
                        :field.sync="subject"
                        :errors="validationMessage($v.subject)"
                        @input.native="$v.subject.$touch()"
                        @blur.native="$v.subject.$touch()"
                    />
                </div>
            </div>
        </div>

        <form-radio-group
            :classes="'support-form_group'"
            :errors="validationMessage($v.type)"
            @input.native="$v.type.$touch()"
            @blur.native="$v.type.$touch()"
        >
            <div
                v-for="(item, index) in types.type"
                :key="index"
                class="support-form_group-item"
            >
                <form-radio
                    v-model="type"
                    :text="item.text"
                    :data="item.value"
                    :is-checked="type === item.value"
                    name="type"
                    :field.sync="type"
                />
            </div>
        </form-radio-group>

        <div
            v-if="type === `complaint`"
            class="complaint_row"
        >
            <div class="col-6 complaint_group">
                <h2>What is the problem?</h2>
                <form-radio-group
                    :errors="validationMessage($v.problem)"
                    @input.native="$v.problem.$touch()"
                    @blur.native="$v.problem.$touch()"
                >
                    <div
                        v-for="(item, index) in types.problem"
                        :key="index"
                        class="complaint_group-item"
                    >
                        <form-radio
                            v-model="problem"
                            :text="item.text"
                            :data="item.value"
                            :is-checked="problem === item.value"
                            name="problem"
                            :field.sync="problem"
                        />
                    </div>
                </form-radio-group>
            </div>

            <div class="col-6 complaint_group">
                <h2>How would you like it resolved?</h2>
                <form-radio-group
                    :errors="validationMessage($v.resolution)"
                    @input.native="$v.resolution.$touch()"
                    @blur.native="$v.resolution.$touch()"
                >
                    <div
                        v-for="(item, index) in types.resolution"
                        :key="index"
                        class="complaint_group-item"
                    >
                        <form-radio
                            v-model="resolution"
                            :text="item.text"
                            :data="item.value"
                            :is-checked="resolution === item.value"
                            name="resolution"
                            :field.sync="resolution"
                        />
                    </div>
                </form-radio-group>
            </div>

            <p class="col-12">
                Before submitting your complaint, please make sure you have read and the
                <a
                    href="https://www.customessaymeister.com/info/terms"
                    target="_blank"
                >terms of service</a> accepted during the ordering process.
            </p>
        </div>

        <div v-if="type === `complaint`">
            <div
                v-if="resolution === `revision` || resolution === `new_writer`"
                class="support-form_notice"
            >
                <ul>
                    <li>
                        The writer must follow the original order guidelines. The writer is not liable to complete
                        revision requests that are beyond the scope of the initial. Requests for additional
                        research/writing are <b>payable</b> and you will need to order extra pages.
                    </li>
                    <li>
                        We are not liable to modify the project based on your professor's feedback. Moreover, we do not
                        guarantee any specific letter grades or academic approval.
                    </li>
                </ul>
            </div>
            <div
                v-if="resolution === `refund` || resolution === `store_credit`"
                class="support-form_notice"
            >
                <ul>
                    <li>
                        Please be specific and state the clause of the
                        <a
                            href="https://www.customessaymeister.com/info/privacy"
                            target="_blank"
                        >agreement</a> that was violated.
                    </li>

                    <li>Please state the refund amount you belive you are entitled to receive</li>
                </ul>
            </div>
        </div>

        <div
            v-if="type"
            class="support-form_message"
        >
            <form-textarea
                v-model="$v.message.$model"
                legend="Message"
                type="text"
                name="problem"
                :field.sync="message"
                :errors="validationMessage($v.message)"
                @input.native="$v.message.$touch()"
                @blur.native="$v.message.$touch()"
            />
        </div>

        <div class="support-form_footer">
            <span
                class="btn-base btn-base_colored btn-base_normal"
                @click.prevent="validateForm()"
            >
                <button-loader v-if="submitLoading" />
                Submit a request
            </span>
        </div>

        <confirm-modal ref="confirm-modal" />
    </div>
</template>

<script>
// Helpers
import { eventBus } from '@/helpers/event-bus/'
import { scroller } from 'vue-scrollto/src/scrollTo'

// Validation
import { validationMixin } from 'vuelidate'
import { validationMessage } from 'vuelidate-messages'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationRules } from '@/validation/support/Rules'
import { formMessages } from '@/validation/support/Messages'

// Icons
import AlertCircleOutline from 'mdi-vue/AlertCircleOutline';

// Components
import ButtonLoader from '@/components/common/ButtonLoader';
import PhoneNumber from '@/components/common/form-elements/phone/PhoneNumber';
import ConfirmModal from '@/components/modals/ConfirmModal';
import ReferenceOrder from '@/components/support/ReferenceOrder';

// Vuex types
import { ACTION_TICKET_SUBMIT } from '@/store/modules/support/action-types';
import { CLEAR_FORM } from '@/store/modules/support/mutation-types';

// vuex bindings
import { mapState, createNamespacedHelpers } from 'vuex'
import { mapSupportFields } from '@/store/modules/support';
import { mapClientFormFields } from '@/store/modules/checkout/index.js'
import { mapClientFields } from '@/store/modules/client'

const {
    mapMutations: mapSupportMutations,
    mapActions: mapSupportActions
} = createNamespacedHelpers('support');

export default {
    components: {
        AlertCircleOutline,
        ButtonLoader,
        ConfirmModal,
        ReferenceOrder,
        PhoneNumber
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    props: {
        propOrderId: {
            type: String
        }
    },
    validations: {
        ...validationRules
    },
    data() {
        return {
            chk_phone_bypass: false,
            submitLoading: false,
            phone_call: false,
            phoneValidationStatus: false,
            types: { type: [{ value: 'inquiry', text: 'Inquiry' }, { value: 'suggestion', text: 'Suggestion' }, { value: 'complaint', text: 'Complaint' }, { value: 'other', text: 'Other' }, { value: 'website', text: 'Technical issue with the website' }], problem: [{ value: 'delivery', text: 'Delivery: The project was delivered late' }, { value: 'billing', text: 'Billing: The billing amount was incorrect' }, { value: 'quality_guidelines', text: 'Quality: The project guidelines were not followed' }, { value: 'quality_writing', text: 'Quality: The writing was not up to standards' }, { value: 'content_pagecount', text: 'Content: The number of pages was incorrect' }, { value: 'other', text: 'Other' }], resolution: [{ value: 'revision', text: 'Revision' }, { value: 'new_writer', text: 'New writer' }, { value: 'discount', text: 'Discount On Next Order' }, { value: 'store_credit', text: 'Store credit' }, { value: 'refund', text: 'Refund partial refund' }, { value: 'other', text: 'Other' }] }
        }
    },
    computed: {
        ...mapClientFields([
            'client_data.id'
        ]),
        ...mapState('client', {
            auth: 'auth'
        }),
        ...mapState('phone', {
            started: 'started',
            verified: 'verified'
        }),
        ...mapClientFormFields([
            'form_data_client.fullname',
            'form_data_client.email'
        ]),
        ...mapClientFields([
            'client_data.firstname'
        ]),
        ...mapSupportFields([
            'form_data.chk_callback',
            'form_data.chk_linked',
            'form_data.linked_to_order',
            'form_data.type',
            'form_data.problem',
            'form_data.resolution',
            'form_data.subject',
            'form_data.message'
        ])
    },
    mounted() {
        if (this.auth) {
            this.fullname = this.$store.state.client.client_data.fullname
            this.email = this.$store.state.client.client_data.email
            this.phone_code = this.$store.state.client.client_data.phone_code
            this.phone = this.$store.state.client.client_data.phone
        }
        if (this.propOrderId) {
            this.linked_to_order = this.propOrderId
            this.chk_linked = true
        }
    },
    methods: {
        ...mapSupportMutations({
            clearForm: CLEAR_FORM
        }),
        ...mapSupportActions({
            submitTicket: ACTION_TICKET_SUBMIT
        }),
        validationMessage: validationMessage(formMessages),
        async openModal() {
            const modalTitle = 'Clear Form'
            const modalText = 'Are you sure you want to delete all data from the order form?'
            const vm = this
            if (await this.$root.$confirm(modalTitle, modalText, { color: 'red' })) {
                vm.$store.commit('checkout/RESET_FORM_DATA_CLIENT')
                vm.clearForm()
                this.$v.$reset()
            }
        },
        validatePhone(value) {
            this.phoneValidationStatus = value
        },
        async validateForm() {
            await this.$v.$touch()
            if (!this.$v.$invalid) {
                this.submit()
            } else {
                const firstErrorElement = document.querySelector('.error-font-color');
                const firstScrollTo = scroller()
                firstScrollTo(firstErrorElement, 200, { offset: -90 })
                this.validate()
            }
        },
        async submit() {
            const vm = this
            try {
                vm.submitLoading = true
                this.submitTicket({}).then(() => {
                    eventBus.$emit('showSnackBar', 'A support ticket has been created. You can view all tickets in your account (Support Tickets).', 'success');
                })
                vm.$store.commit('checkout/RESET_FORM_DATA_CLIENT')
                vm.$store.commit('support/CLEAR_FORM')
                vm.chk_phone_bypass = false
                const firstScrollTo = scroller()
                firstScrollTo(document.body, 200)
                vm.$v.$reset()
            } catch (error) {
                console.log(error);
                eventBus.$emit('showSnackBar', error.response.data.message, 'error');
            } finally {
                this.submitLoading = false
            }
        }
    }
}
</script>
