<template>
    <div
        class="form-select"
        :class="{ 'error-border': validationError }"
    >
        <fieldset class="form-select__fieldset">
            <legend
                class="form-select__legend"
                :class="{ 'error': err }"
            >
                {{ label }}
            </legend>
            <component
                :is="prependIcon"
                class="form-input__prepend-icon"
            />
            <input
                class="form-select__input"
                :value="setTextByValue"
                type="text"
                :name="name"
                @click="openListItem = !openListItem"
                @focus="$event.target.select()"
                @input="onInputChanged($event)"
            >
            <ul
                v-if="openListItem"
                class="form-select__list"
            >
                <li
                    v-for="(item, index) in filteredList"
                    :key="index"
                    class="form-select__list-item"
                    @click="selectItem(item.orderid)"
                >
                    <span class="form-select__list-item-title">
                        «{{ item.title }}»
                    </span>
                    <span class="form-select__list-item-date">
                        {{ item.created_at | moment_from }} ago {{ item.orderid }}, {{ item.status }}
                    </span>
                </li>
            </ul>
            <div
                v-if="validationError"
                class="form-select__error"
            >
                {{ validationError }}
            </div>
        </fieldset>
    </div>
</template>

<script>
import BookOpenVariant from 'mdi-vue/BookOpenVariant';
import FileDocument from 'mdi-vue/FileDocument';
import filtersMixin from '@/mixins/filtersMixin'

export default {
    name: 'FormSelect',
    components: {
        BookOpenVariantIcon: BookOpenVariant,
        FileDocumentIcon: FileDocument
    },
    mixins: [
        filtersMixin
    ],
    props: {
        value: {
            type: String
        },
        prependIcon: {
            type: String
        },
        items: {
            type: Array
        },
        label: {
            type: String
        },
        errors: {
            type: String
        },
        name: {
            type: String
        },
        err: {
            type: Boolean
        }
    },
    data() {
        return {
            openListItem: false,
            searchItemName: null,
            filteredItems: [],
            search: '',
            initValue: null
        };
    },
    computed: {
        setItems() {
            return this.searchItemName === null ? this.items : this.filteredItems;
        },
        setTextByValue() {
            this.setData();
            const isExist = this.items.some((el) => el.orderid === this.initValue);
            if (isExist) {
                return this.items.find((element) => element.orderid === this.initValue).orderid;
            }
            return this.search;
        },
        filteredList() {
            return this.items.filter((item) => item.title.toLowerCase().includes(this.search.toLowerCase()));
        },
        validationError() {
            return this.errors ? this.errors : '';
        }
    },
    created() {
        this.setInitValue();
    },
    methods: {
        async selectItem(value) {
            await this.$emit('input', value);
            await this.setInitValue();
            this.openListItem = false;
            this.search = '';
        },
        onInputChanged(event) {
            this.openListItem = true;
            this.search = this.initValue;
            this.initValue = event.target.value;
            this.$emit('input', event.target.value);
        },
        setInitValue() {
            this.initValue = this.value;
        },
        setData() {
            this.initValue === null ? this.initValue = this.value : 'this.initValue';
        }
    }
};
</script>

